.headertopbar-header {
    background-color: #242424;
}

.header-nav {
    padding: 15px 0 90px;
    position: relative;
}

    /* marquee container */
    .marquee-wrap {
        width: 100%;
        max-width: 1000px;
        overflow: hidden;
        border-radius: 14px;
        padding: 18px;
        background: rgba(255,255,255,0.03);
        box-shadow: 0 6px 30px rgba(2,6,23,0.6);
    }

    /* the moving track */
    .marquee {
        display: flex;
        gap: 16px;
        align-items: center;
    }

    .marquee-track {
        display: flex;
        gap: 16px;
        align-items: center;
        animation: scroll 18s linear infinite;
        will-change: transform;
    }

    /* pause on hover */
    .marquee-wrap:hover .marquee-track {
        animation-play-state: paused;
    }

    @keyframes scroll {
        0% { transform: translateX(0); }
        100% { transform: translateX(-50%); }
    }

    /* RTL: reverse the scroll direction */
    [dir="rtl"] .marquee-track {
        animation-name: scroll-rtl;
    }
    @keyframes scroll-rtl {
        0% { transform: translateX(0); }
        100% { transform: translateX(50%); }
    }

    /* card/item */
    .fruit-card {
        display: flex;
        flex-direction: row;
        align-items: center;
        min-width: 170px;
        padding: 14px;
        border-radius: 12px;
        background: #1a1a1a;
        box-shadow: 0 8px 18px rgba(2,6,23,0.6);
        gap: 14px;
    }

    /* RTL: flip card internals */
    [dir="rtl"] .fruit-card {
        flex-direction: row-reverse;
    }

    .fruit-svg { width: 68px; height: 68px; }
    .fruit-name { flex-shrink: 0; }
    .fruit-sub { font-size: 0.86rem; opacity: 0.75; }

    /* spacing control so duplicated track aligns seamlessly */
    .marquee-track > .spacer {
        width: 16px;
        flex: 0 0 16px;
    }

    /* accessibility: respects reduced motion */
    @media (prefers-reduced-motion: reduce) {
        .marquee-track { animation: none; }
    }

    /* responsive tweaks */
    @media (max-width: 520px) {
        .fruit-card { min-width: 140px; padding: 10px; }
        .fruit-svg { width: 56px; height: 56px; }
    }